  margin: 0;
  padding: 0;
}
body{
  background: #000;
}
img{
  border:0;
  border-radius: 3px;
}
ul{
  list-style: none;
}
.box{
  width: 212px;
  border: 5px solid #fff;
  overflow: hidden;
  zoom: 1;
  background: #fff;
  border-radius: 5px;
  margin: 10px auto;
  padding: 0 0 3px 3px;
}
.box li, .box li img{
  float: left;
  width: 50px;
  height: 50px;
}
.box li{
  margin: 3px 3px 0 0;
}
.box li.first{
  position: relative;
}
.box li.first, .box li.first img{
  width: 156px;
  height: 156px;
}
.box li.first div{
  position: absolute;
  top:0;
  left: 0;
  width: 156px;
  height: 156px;
  display: none;
  opacity: 0.5;
  filter: alpha(opacity=0.5);
  background:#fff url(./assets/lesson2/loading.gif) 50% 50% no-repeat;
}
.box li.first div.current{
  display: block;
}
</style>
